import moment from 'moment';

const locale = {
  months: 'Janeiro_Fevereiro_Maro_Abril_Maio_Junho_Julho_Agosto_Setembro_Outubro_Novembro_Dezembro'
    .split('_'),
  monthsShort: 'Jan_Fev_Mar_Abr_Mai_Jun_Jul_Ago_Set_Out_Nov_Dez'
    .split('_'),
  weekdays: 'Domingo_Segunda-feira_Tera-feira_Quarta-feira_Quinta-feira_Sexta-feira_Sbado'
    .split('_'),
  weekdaysShort: 'Dom_Seg_Ter_Qua_Qui_Sex_Sb'
    .split('_'),
  weekdaysMin: 'Dom_2_3_4_5_6_Sb'
    .split('_'),
  weekdaysParseExact: true,
  longDateFormat: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'DD/MM/YYYY',
    LL: 'D [de] MMMM [de] YYYY',
    LLL: 'D [de] MMMM [de] YYYY [s] HH:mm',
    LLLL: 'dddd, D [de] MMMM [de] YYYY [s] HH:mm'
  },
  calendar: {
    sameDay: '[Hoje s] LT',
    nextDay: '[Amanh s] LT',
    nextWeek: 'dddd [s] LT',
    lastDay: '[Ontem s] LT',
    lastWeek() {
      return (this.day() === 0 || this.day() === 6) ?
        '[ltimo] dddd [s] LT' : // Saturday + Sunday
        '[ltima] dddd [s] LT'; // Monday - Friday
    },
    sameElse: 'L',
  },
  relativeTime: {
    future: 'em %s',
    past: '%s atrs',
    s: 'poucos segundos',
    m: 'um minuto',
    mm: '%d minutos',
    h: 'uma hora',
    hh: '%d horas',
    d: 'um dia',
    dd: '%d dias',
    M: 'um ms',
    MM: '%d meses',
    y: 'um ano',
    yy: '%d anos',
  },
  ordinalParse: /\d{1,2}/,
  ordinal: '%d',
};

export default moment.defineLocale('pt-br', locale);



