import numeral from 'numeral';

const locale = {
  delimiters: {
    thousands: '.',
    decimal: ',',
  },
  abbreviations: {
    thousand: 'mil',
    million: 'milhes',
    billion: 'b',
    trillion: 't',
  },
  ordinal(number) {
    return '';
  },
  currency: {
    symbol: 'R$',
  },
};

export default numeral.register('locale', 'pt-br', locale);



